import React, { Component } from 'react';

const colors = [
  {
    color: 'lilac',
    code: '#F171F2'
  },
  {
    color: 'brown',
    code: '#FED085'
  },
  {
    color: 'pink',
    code: '#FE7998'
  },
  {
    color: 'grey',
    code:  '#CED9DE'
  },
  {
    color: 'blue',
    code:  '#86D8F9'
  },
  {
    color: 'orange',
    code: '#FFFD37'
  },
  {color: 'aqua',
  code: '#7BFEF6'
},
  {
    color: 'green',
    code: '#84F47A'}
]
export default class Colors extends Component {
  render() {
    const circles = colors.map(color => <span key={color.color} className='circle' style={{backgroundColor: color.code}}></span>)
    return (
      <div>
        {circles}
      </div>
    )
  }
}


